.fullImg {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
  opacity: 0.5;

  background-size: cover;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  position: fixed;
}

.year_data{
margin-left: 7rem;
}

.rating_icon{
    color: #c6ae46;
}

.rating_data{
    font-size: 1.2rem;
    margin-left: 0.3rem;
}

.info_title{
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-self: center;
}

.info_img{
    border-radius: 0.5rem;
}

.box {
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: center;

  width: 43rem;
  height: 30rem;
}

.t_container {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
}

.md_container {
  border: 0.0625rem solid rgb(255 255 255 / 21%);
  border-radius: 5rem;
  width: 50rem;
  height: 30rem;
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
  background-color: #8181819c;
}

.info_container{
    padding: 3rem;
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
}


.genres_container{
    list-style: none;
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    /* background-color: rgb(0, 0, 0); */
    padding: 0.3rem;
}

.genres{
    background-color: rgb(32 14 63);
    border-radius: 0.5rem;
    padding: 0.5rem;
    margin: 0.2rem;
}

@media screen and (max-width: 1090px) {
  .movie-container {
    grid-template-columns: 1fr;
    width: 100%;
    padding:0;
  }
}

.movieContainer {
  flex-direction: row;
  justify-content: center;
}

.link {
  text-decoration: none;
  color: white;
  border-radius: 1rem;
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.coverImg {
  border-radius: 2rem;
  width: 12rem;
  padding: 1rem;
}

.coverImg:hover {
  -webkit-filter: brightness(0.5);
          filter: brightness(0.5);
  transition-duration: 1s;
}

.title {
  width: 12rem;
  height: 5rem;
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

body {
  margin: 0;
  left: 0;
  top: 0;
}

.header-container{
  padding-top: 1.2rem;
}

.movie-container {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  background-color: black;
  padding: 2.5rem;
  grid-gap: 3rem;
}

.header {
  background-color: #17172f94;
  font-size: 2rem;
  height: 5rem;
  border-bottom: 0.0625rem solid rgb(255 255 255 / 79%);
}

.header a {
  color: #8442fb;
  margin-left: 3rem;
  text-decoration: none;
}

.loading {
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
}


@media screen and (max-width: 1090px) {
  .movie-container {
    grid-template-columns: 1fr;
    width: 100%;
    padding:0;
  }
}
